import React from "react";
import Color from "./Color";
import data from "./../data/colors";
import { FixedSizeList as List } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";



export default class ReactWindow extends React.Component {
    constructor(props) {
        super(props);
        
    }
    render(){
        return(
            <div className='container-fluid'>
                 <AutoSizer>
                    {({ height, width }) => (
                    <List
                        className="List"
                        height={height}
                        itemCount={1000}
                        itemSize={35}
                        width={width}
                    >
                        {height}
                    </List>
                    )}
                </AutoSizer>
            </div>
        )
    }
   
}



